import React from 'react'
import s from './Users.module.css'
import Users from './Users'
import {connect} from 'react-redux'
import {follow, unfollow, getUsersThunkCreator, changeUsers} from '../../redux/usersReducer'
import { compose } from 'redux'
import { getPagesize, getTotalUsersCount, getCurrentPage, getIsFetching, getFollowingInProgress, getIsAuth, getUsersSuperSelector } from './usersSelectors'
import { WithAuthRedirect } from '../../hoc/withAuthRedirect'
import { UserType } from '../../types/types'
import { AppStateType } from '../../redux/redux-store'

type MapStatePropsType = {
    usersData: Array<UserType>,
    pageSize: number,
    totalUsersCount: number,
    currentPage: number,
    isFetching: boolean,
    followingInProgress: Array<number>,
    isAuth: boolean
}

type MapDispatchPropsType = {
    follow: (userId: number) => void,
    unfollow: (userId: number) => void,
    getUsers:  (currentPage: number, pageSize: number) => void,
    changeUsers: (pageNumber: number, pageSize: number) => void
}

type PropsType = MapStatePropsType & MapDispatchPropsType

class UsersContainer extends React.Component<PropsType> {
    componentDidMount() {
        this.props.getUsers(this.props.currentPage, this.props.pageSize)
    }

    onPageChanged: (pageNumber: number) => void = (pageNumber: number) => {
        this.props.changeUsers(pageNumber, this.props.pageSize)
    }

    render() {
        return <>
        {this.props.isFetching ? <img className={s.toggleLoader} src='https://брайтфит.рф/style/images/i-loader.png' alt='load' /> : null}
        <Users
        totalUsersCount={this.props.totalUsersCount}
        pageSize={this.props.pageSize}
        usersData={this.props.usersData}
        unfollow={this.props.unfollow}
        follow={this.props.follow}
        currentPage={this.props.currentPage}
        onPageChanged={this.onPageChanged}
        followingInProgress={this.props.followingInProgress}/> </>
    }
}

let mapStateToProps = (state: AppStateType): MapStatePropsType => {
    return {
        usersData: getUsersSuperSelector(state),
        pageSize: getPagesize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state),
        isAuth: getIsAuth(state),
    }
}

let mapDispatchToProps: MapDispatchPropsType = {
    follow, unfollow, getUsers: getUsersThunkCreator, changeUsers
}

export default compose(
    connect(mapStateToProps, mapDispatchToProps),
    WithAuthRedirect
)(UsersContainer)