import React from 'react'
import s from './Users.module.css'
import {NavLink} from 'react-router-dom'
import Paginator from '../common/Paginator/Paginator'
import { UserType } from '../../types/types'

type PropsType = {
    currentPage: number
    onPageChanged: (pageNumber: number) => void
    totalUsersCount: number
    pageSize: number
    usersData: Array<UserType>
    followingInProgress: Array<number>
    unfollow: (userId: number) => void
    follow: (userId: number) => void,
    
}

const Users: React.FC<PropsType> = ({currentPage, onPageChanged, totalUsersCount, pageSize, usersData, ...props}) => {
    let pagesCount = Math.ceil(totalUsersCount / pageSize)

    let pages = []

    for(let i=1; i <= pagesCount; i++) {
        pages.push(i)
    }
    
    return <div>
        <Paginator currentPage={currentPage} onPageChanged={onPageChanged} totalUsersCount={totalUsersCount} pageSize={pageSize}/>
    {
        usersData.map(u => <div className={s.usersArea} key={u.id}>
            <span>
                <NavLink to={'/profile/'+ u.id}><div><img alt='ava' src={u.photos.small ? u.photos.small : 'https://upload.wikimedia.org/wikipedia/commons/thumb/7/7e/Circle-icons-profile.svg/768px-Circle-icons-profile.svg.png'} className={s.photo}/></div></NavLink>
                <div>{u.name}</div>
                <div>{u.status}</div>
            </span>

            <span>
                <div>
                    {u.followed
                        ? <button disabled={props.followingInProgress.some(id => id === u.id)} onClick={() => {
                            props.unfollow(u.id)
                        }
                        }>Unfollow</button>
                        : <button disabled={props.followingInProgress.some(id => id === u.id)} onClick={() => {
                            props.follow(u.id)
                        }
                        }>Follow</button>}
                </div>
            </span>
        </div>
        
        )
    }

    </div>
}

export default Users